import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";

import project1 from "../images/html-css-javascript-lg.jpg";
import project2 from "../images/html-css-javascript.jpg";
import project3 from "../images/javascript-fullstack.jpg";
import project4 from "../images/mern-stack.jpg";
import project5 from "../images/react-redux.jpg";
import project6 from "../images/react.png";

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    background: "#233",
    height: "100%",
  },
  cardContainer: {
    maxWidth: 345,
    margin: "3rem auto",
  },
}));

const projects = [
  {
    name: "Project 1",
    description: `A Doctor Appointemt Booking System is a web-based platform
    that allows patients to schedule appointments with thier doctors online.
    with this system ,patients can easily book appointments with thier preferred
    doctors at any time of the day and it offers several benefits to both patients
    and health care providers`,
    image: project1,
  },
  {
    name: "Project 2",
    description: `A Data Visualization using flask. 
    Embark on a journey of seamless user interaction with our project, aimed at providing
     an intuitive experience for effortlessly inputting data. Our goal is to empower users by offering 
    a streamlined process, enabling them to gain valuable insights. Through dynamic visualizations`,
    image: project2,
  },
  {
    name: "Project 3",
    description: `Data analytics for a preloaded dataset.
    The react application that shows the data analytics for a preloaded dataset , the backend
     is implemented using python and flask with,
    the data being stored in a mongo database and read through the use of Pymongo. And we can use different visualization techniques
    to show the output`,
    image: project3,
  },
  {
    name: "Project 4",
    description: `Object storage system.
     a robust platform designed for efficient storage and retrieval of objects. 
    This project is a comprehensive endeavor aiming to integrate Minio seamlessly with your tech stack. 
    Utilize the power of React.js for a dynamic and user-friendly frontend,
     while employing Node.js and Python on the backend for robust functionality.
     Enable your application to effortlessly retrieve and store files in your Minio storage setup.`,
    image: project4,
  },
  {
    name: "Project 5",
    description: `A comprehensive React-based webpage featuring essential components:
    1.Navbar:Enable seamless navigation.
    2.Sidebar:Enhance user-friendliness.
    3.SearchBar:Facilitate efficient data retrieval.
    4.Cards:Present visually appealing content.
    5.Piecharts:Visualize key data insights.
    6.Table:Display structured tabular data.
    7.Form:Incorporate diverse input elements.
    8.Footer:Provide contextual information.`,
    image: project5,
  },
  {
    name: "Project 6",
    description: `A dynamic and personalized portfolio website using react
    showcasing your skills, projects, and professional journey. The portfolio will include:
     home section,skills section,projects section,contact section
     responsive design, interactive elements,custom styilng ,tech stack 
     The portfolio serves as a reflection of your professional identity, providing visitors with a comprehensive view of your expertise and accomplishments.`,
    image: project6,
  },
];

const Portfolio = () => {
  const classes = useStyles();
  return (
    <Box component="div" className={classes.mainContainer}>
      <Grid container justify="center">
        {/* Projects */}
        {projects.map((project, i) => (
          <Grid item xs={12} sm={8} md={4} key={i}>
            <Card className={classes.cardContainer}>
              <CardActionArea>
                <CardMedia
                  component="img"
                  alt="Project 1"
                  height="140"
                  image={project.image}
                />
                <CardContent>
                  <Typography variant="h5" gutterBottom>
                    {project.name}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    {project.description}
                  </Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
               
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default Portfolio;
