import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    background: "",
    height: "10%",
  },
  heading: {
    color: "tomato",
    padding: "3rem 0",
    textTransform: "uppercase",
  },
  skillsBox: {
    display: "flex",
    justifyContent: "space-around",
    flexWrap: "wrap",
    marginTop: "10rem",
  },
  skillBox: {
    width: "45%",
    padding: "1rem",
    border: "4px solid tomato",
    borderRadius: "10px",
    margin: "1rem",
    textAlign: "center",
  },
  skillText: {
    color: "#fff",
  },
}));

const Skills = () => {
  const classes = useStyles();

  return (
    <Box component="div" className={classes.mainContainer}>
      <Typography variant="h4" align="center" className={classes.heading}>
        Skills
      </Typography>
      <Box component="div" className={classes.skillsBox}>
        {/* Frontend Development Skills */}
        <Box component="div" className={classes.skillBox}>
          <Typography variant="h4" className={classes.skillText}>
            Frontend Development Skills
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - HTML
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - CSS
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - JavaScript
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - React
          </Typography>
        </Box>

        {/* Data Analytics Skills */}
        <Box component="div" className={classes.skillBox}>
          <Typography variant="h4" className={classes.skillText}>
            Data Analytics Skills
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - Python
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - Pandas
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - SQL
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - PowerBI
          </Typography>
          <Typography variant="body1" className={classes.skillText}>
            - Data Visualization
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};

export default Skills;
